import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.scss']
})
export class EmployeeComponent  {
  constructor(
    private router: Router, 
    private vhAuth: VhAuth,
    public languageService: LanguageService,
    public vhComponent: VhComponent
  ) {
    
  } 
  gotoEmployeeList(){
    if (this.vhAuth.checkMyPermission("management_enable_employee")) this.router.navigate(['/sales/dashboard/manage/partner/employee/employee-list'])
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
    
  }

  gotoEmployeeGroup(){
    if (this.vhAuth.checkMyPermission("management_enable_employee_group")) this.router.navigate(['/sales/dashboard/manage/partner/employee/employee-group'])
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
    
  }

  goBack(){
    this.router.navigate(['/sales/dashboard/manage/partner'])
  }

}
